.b-devices {
  position: relative;
  background: white; }
  .b-devices__wrap {
    display: flex; }
  .b-devices__block {
    flex: 0 0 50%;
    max-width: 50vw;
    min-width: 0;
    position: relative; }
  .b-devices__slider {
    max-width: 100%;
    max-height: 100%; }
    .b-devices__slider-arrows {
      position: absolute;
      bottom: 132px;
      width: 100px;
      margin: auto;
      left: 0;
      right: 0;
      display: flex;
      justify-content: space-between; }
    .b-devices__slider .item {
      position: relative;
      height: 100vh;
      background: center/cover no-repeat; }
      .b-devices__slider .item__name {
        position: absolute;
        top: 50%;
        left: 100px;
        transform: translateY(-50%);
        max-height: 30px; }

.arrow {
  cursor: pointer; }
